:root {
  --bg-body: hsl(0, 0%, 98%);
  --hero-img: url(./images/bg-desktop-light.jpg);
  --bg-section: #fff;
  --txt-color: hsl(235, 19%, 35%);
  --txt-color-secondary: hsl(236, 9%, 61%);
  --border-color: hsl(233, 11%, 84%);
  --completed-txt-clr: hsl(233, 11%, 84%);
  --background-transition: background 350ms;
}

/* ------------------- */
/* Reset               */
/* ------------------- */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 18px;
}

/* set up the body */
body {
  font-family: 'Josefin Sans', sans-serif;
  line-height: 1.4;
  background-color: var(--bg-body);
  color: var(--txt-color);
  min-height: 100vh;
  transition: var(--background-transition);
}

/* make images easier to work with */
img,
picutre {
  display: block;
  max-width: 100%;
  cursor: pointer;
}

/* make form elements easier to work with */
input,
button,
textarea,
select {
  font: inherit;
}

button {
  background: transparent;
  color: inherit;
  border: none;
}

button:not(:last-child) {
  margin-right: .75rem;
}

/* ------------------- */
/* Utility classes     */
/* ------------------- */
.container {
  max-width: 700px;
  margin-inline: auto;
  padding-inline: 1rem;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.custom-checkbox {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid var(--border-color);
  cursor: pointer;
}

/* ------------------- */
/*Main Section Styling */
/* ------------------- */
main {
  display: flow-root;
  background: var(--hero-img) top center/cover;
  min-height: 400px;
  transition: var(--background-transition);
}

main:has(.toggle-dark-mode:checked) {
  --hero-img: url(./images/bg-desktop-dark.jpg);
}

main .flex {
  margin-top: 5rem;
}

main h1 {
  color: #fff;
  letter-spacing: 1rem;
}

.toggle-dark-mode {
  background-image: url(./images/icon-moon.svg);
  width: 26px;
  height: 26px;
  cursor: pointer;
  appearance: none;
}

.toggle-dark-mode:checked {
  background-image: url(./images/icon-sun.svg);
}

/* todos section styling */
.todos {
  margin-top: -10%;
}

.addtodo {
  position: relative;
  margin-bottom: 1rem;
}

.select-all {
  position: absolute;
  left: 1rem;
}

.addtodo input {
  width: 100%;
  padding: 1rem 1rem 1rem 4rem;
  border: 0;
  border-radius: .5rem;
  background-color: var(--bg-section);
  transition: var(--background-transition);
}

.addtodo input::placeholder {
  color: var(--txt-color-secondary);
}

.tasklist {
  background: var(--bg-section);
  border-top-left-radius: .5rem;
  border-top-right-radius: .5rem;
  box-shadow: 1px 60px 30px 15px #00000030;
  transition: var(--background-transition);
}

.task {
  list-style-type: none;
  padding: 1rem;
  border-bottom: 1px solid var(--border-color);
  justify-content: flex-start;
  gap: .5rem;
  cursor: grab;
}

.task:first-child {
  border-top-left-radius: .5rem;
  border-top-right-radius: .5rem;
}

.task > *:nth-child(2) {
  flex-grow: 1;
  transition: color 350ms;
}

.completed {
  background:
    url(./images/icon-check.svg) no-repeat center/14px,
    linear-gradient(140deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}

.completed + p {
  text-decoration: line-through;
  color: var(--completed-txt-clr);
}

.delete {
  visibility: hidden;
}

.task:hover .delete {
  visibility: visible;
}

/* Additional info section styling */
.filter-todos {
  background-color: var(--bg-section);
  color: var(--txt-color-secondary);
  font-size: .9rem;
  padding: 1rem;
  margin-bottom: 5%;
  border-bottom-left-radius: .5rem;
  border-bottom-right-radius: .5rem;
}

.filter-todos button {
  cursor: pointer;
}

.drag {
  text-align: center;
  font-size: .9rem;
  color: var(--txt-color-secondary);
  margin-top: 10%;
}

.mob {
  display: none;
}

@media screen and (max-width: 500px) {
  main {
    --hero-img: url(./images/bg-mobile-light.jpg);
    min-height: 200px;
  }

  main:has(.toggle-dark-mode:checked) {
    --hero-img: url(./images/bg-mobile-dark.jpg);
  }

  main h1 {
    letter-spacing: .7rem;
    font-size: 1.5rem;
  }

  main .flex {
    margin-top: 2.5rem;
  }

  .todos {
    margin-top: -23%;
  }

  .tasklist {
    font-size: .8rem;
  }

  .task .delete {
    visibility: visible;
  }

  .filter-todos div:nth-child(2) {
    display: none;
  }

  .mob {
    display: block;
    background-color: var(--bg-section);
    padding-block: .75rem;
    text-align: center;
  }
}

/* Dark mode */
:root:has(.toggle-dark-mode:checked) {
  --bg-body: hsl(235, 21%, 11%);
  --bg-section: hsl(235, 24%, 19%);
  --txt-color: hsl(234, 39%, 85%);
  --border-color: hsl(233, 14%, 35%);
  --completed-txt-clr: hsl(234, 11%, 52%);
}